export function filtersReducer(filters, action) {
  switch (action.type) {
    case 'addFaction': {
      filters.factions.add(action.filter);
      return Object.assign({}, filters);
    }
    case 'removeFaction': {
      filters.factions.delete(action.filter);
      return Object.assign({}, filters);
    }
    case 'addSystem': {
      filters.systems.add(action.filter);
      return Object.assign({}, filters);
    }
    case 'removeSystem': {
      filters.systems.delete(action.filter);
      return Object.assign({}, filters);
    }
    case 'addEngagement': {
      filters.engagements.add(action.filter);
      return Object.assign({}, filters);
    }
    case 'removeEngagement': {
      filters.engagements.delete(action.filter);
      return Object.assign({}, filters);
    }
  }
};

