import { useState } from 'react';
import DOMPurify from 'dompurify';
import {
  cards,
  description,
  gallery,
  headline,
  narration,
  separator,
  subheadline,
  quotation,
  thumbnail,
  defaultGalleryWidth,
  defaultThumbWidth,
  right,
} from './constants';
import {
  StyledHeader,
  StyledSubheader,
  StyledSubheader3,
  StyledSubheader4,
  StyledParagraph,
} from '../styled';

function Card({ cardData }) {
  const [isOpen, setIsOpen] = useState(false);
  const {
    subheads, subheads2, subheads3, paragraphs, expanded, image
  } = cardData;
  return (
    <section
      className={expanded ? "planet-section pointer" : "planet-section"}
      onClick={() => setIsOpen(!isOpen)}
    >
      {subheads?.map((text, i) => (
        <StyledSubheader key={`s1-${i}`}>
          {text}
        </StyledSubheader>
      )
      )}
      {subheads2?.map((text, i) => (
        <StyledSubheader3 key={`s2-${i}`}>
          {text}
        </StyledSubheader3>
      )
      )}
      {subheads3?.map((text, i) => (
        <StyledSubheader4 key={`s4-${i}`}>
          {text}
        </StyledSubheader4>
      )
      )}
      {paragraphs?.map((text, i) => (
        <StyledParagraph key={`sp-${i}`}>
          {text}
        </StyledParagraph>
      )
      )}
      {expanded && isOpen && expanded.map((text, i) => (
        <StyledParagraph key={`se-${i}`}>
          {text}
        </StyledParagraph>
      )
      )}
      {image && <img src={image} alt={"img"} />}
    </section>
  )
}

function SectionParagraph({ paragraph, openModal }) {
  const { type } = paragraph;
  const extraClasses = paragraph.classes?.join(" ");
  switch (type) {
    case description:
      const sanitizedText = () => ({
        __html: DOMPurify.sanitize(paragraph.text)
      })
      return (
        <p className={extraClasses} dangerouslySetInnerHTML={sanitizedText()} />
      );
    case headline:
      return (
        <StyledSubheader3 className={extraClasses}>
          {paragraph.text}
        </StyledSubheader3>
      );
    case narration:
      return (
        <aside className="p-quote-card italic">
          {paragraph.text.map(function (line, i) {
            return (
              <StyledParagraph
                key={`narration-${i}`}
                className={extraClasses}
              >{line}</StyledParagraph>
            )
          })}
        </aside>
      )
    case subheadline:
      return (
        <StyledSubheader4 className={extraClasses}>
          {paragraph.text}
        </StyledSubheader4>
      );
    case quotation:
      return (
        <aside className="p-quote-card">
          <div className="p-quote">
            {paragraph.text.map(function (line, i) {
              return (
                <StyledParagraph
                  key={`quote-${i}`}
                  className={extraClasses}
                >{line}</StyledParagraph>
              )
            })}
          </div>
          {paragraph.speaker && <StyledParagraph className={extraClasses}>
            &#8212; {paragraph.speaker}</StyledParagraph>}
        </aside>
      );
    case thumbnail:
      const { caption, image } = paragraph;
      const align = paragraph.align || right;
      const widthFactor = paragraph.widthFactor || 1.0;
      let width = Math.round((defaultThumbWidth * widthFactor) / 10) * 10;
      if (width < 100) { width = 100; }
      if (width > 500) { width = 500; }
      const sanitizedTCaption = () => ({
        __html: DOMPurify.sanitize(caption)
      })
      return (
        <div className={`thumb t${align}`}>
          <div className="thumbinner" style={{ width: width + 'px' }}>
            <a className="image" role="button" onClick={() => openModal(image, caption)}>
              <img className="thumbimage" src={image} />
            </a>
            <div
              className="thumbcaption"
              dangerouslySetInnerHTML={sanitizedTCaption()}
            />
          </div>
        </div>
      );
    case gallery:
      return (
        <ul className="gallery">
          {paragraph.galleryboxes.map(function (item, i) {
            const { image, caption } = item;
            // const widthFactor = paragraph.widthFactor || item.widthFactor || 1.0;
            // const heightFactor = paragraph.heightFactor || item.heightFactor || 1.0;
            // let width = Math.round((defaultGalleryWidth * widthFactor) / 10) * 10;
            // if (width < 100) { width = 100; }
            // if (width > 500) { width = 500; }
            const sanitizedGCaption = () => ({
              __html: DOMPurify.sanitize(caption)
            })
            return (
              <li className="gallerybox" key={`gb-${i}`}>
                <div className="thumb">
                  <div className="gallery-img-wrapper">
                    <a
                      className="image"
                      role="button"
                      onClick={() => openModal(image, caption)}
                    >
                      <img className="thumbimage" src={image} />
                    </a>
                  </div>
                </div>
                <div className="gallerytext">
                  <p dangerouslySetInnerHTML={sanitizedGCaption()} />
                </div>
              </li>
            )
          })}
        </ul>
      );
    case cards:
      return (
        <ul className="cards-gallery">
          {paragraph.cards.map(function (item, i) {
            return (
              <li key={`card-${i}`}>
                <Card cardData={item} />
              </li>
            )
          })}
        </ul>
      );
    case separator:
      return (
        <img src={paragraph.separator} className="separator" alt="" />
      );
    default:
      return null;
  }
}

export default SectionParagraph;