import { useState, useReducer, useRef } from 'react';
import {
  filtersReducer,
  initialFilters,
  factionsList,
} from '../events/filtersReducer';
import CharacterProfile from './CharacterProfile';
import { leaders, agents, artifacts } from './characterData';
import { StyledHeader } from '../styled';
import './characters.css';

function CharacterFilter({
  filters,
  dispatch,
  showFactions,
  toggleShowFactions,
  results,
}) {
  function filterText(set) {
    if (set.size === 0) {
      return 'All';
    }
    return Array.from(set).join(", ");
  }

  function toggleFaction(faction) {
    const actionType = filters.factions.has(faction) ? 'removeFaction' : 'addFaction';
    dispatch({ type: actionType, filter: faction });
  }

  return (
    <div className="event-filters">
      <div className="filter-group">
        <div className="filter-summary" onClick={toggleShowFactions}>
          <span>Factions: {filterText(filters.factions)}</span>
          <i className={`arrow ${showFactions ? 'up' : 'down'}`} />
        </div>
        <ul className={showFactions ? "filter-options" : "hidden"}>
          {Object.values(factionsList).map(faction => {
            return (
              <li key={`faction-${faction}`} className="filter-option">
                <label>
                  <input
                    type="checkbox"
                    className="filter-checkbox"
                    defaultChecked={filters.factions.has(faction)}
                    onClick={() => toggleFaction(faction)}
                  />
                  {faction}
                </label>
              </li>
            );
          })}
        </ul>
      </div>

      <div className="filter-group">
        <div className="filter-summary">
          <span>+++Records returned: {results}+++</span>
        </div>
      </div>
    </div>
  )
}

function Characters() {
  const [filters, dispatch] = useReducer(filtersReducer, initialFilters);
  const [showFactions, setShowFactions] = useState(false);
  function toggleShowFactions() {
    setShowFactions(!showFactions);
  }
  let leaderList = [...leaders];
  let agentList = [...agents];
  let artifactList = [...artifacts];

  if (filters.factions.size) {
    leaderList = leaderList.filter((c) => {
      return c.factions.some((faction) => filters.factions.has(faction))
    });

    agentList = agentList.filter((c) => {
      return c.factions.some((faction) => filters.factions.has(faction))
    });


    artifactList = artifactList.filter((c) => {
      return c.factions.some((faction) => filters.factions.has(faction))
    });
  }

  const leadersRef = useRef(null);
  const operativesRef = useRef(null);
  const artifactsRef = useRef(null);

  const barHeight = '50px';
  function scrollRefWithOffset(scrollRef) {
    scrollRef.current.style.scrollMargin = barHeight;
    scrollRef.current.scrollIntoView({ behavior: 'smooth' });
  }

  return (
    <>
      <nav className="sub-nav">
        <ul className="navigation">
          <li onClick={() => scrollRefWithOffset(leadersRef)}>Leaders</li>
          <li onClick={() => scrollRefWithOffset(operativesRef)}>Operatives</li>
          <li onClick={() => scrollRefWithOffset(artifactsRef)}>Artifacts</li>
        </ul>
      </nav>


      <CharacterFilter
        filters={filters}
        dispatch={dispatch}
        showFactions={showFactions}
        toggleShowFactions={toggleShowFactions}
        results={leaderList.length + agentList.length + artifactList.length}
      />

      <StyledHeader ref={leadersRef}>Leaders</StyledHeader>
      <ul className="char-list">
        {leaderList.map(function (character, index) {
          return (
            <li key={`leader-${index}`} className="char-li">
              <CharacterProfile character={character} />
            </li>
          );
        })}
      </ul>

      <StyledHeader ref={operativesRef}>Operatives</StyledHeader>
      <ul className="char-list">
        {agentList.map(function (character, index) {
          return (
            <li key={`agent-${index}`} className="char-li">
              <CharacterProfile character={character} />
            </li>
          );
        })}
      </ul>

      <StyledHeader ref={artifactsRef}>Artifacts</StyledHeader>
      <ul className="char-list">
        {artifactList.map(function (character, index) {
          return (
            <li key={`artifact-${index}`} className="char-li">
              <CharacterProfile character={character} />
            </li>
          );
        })}
      </ul>
    </>
  );
}

export default Characters;