import { useState } from 'react';
import ImageModal from '../ImageModal';
import SectionParagraph from '../shared/SectionParagraph';
import allEvents from './eventPageData/index';
import {
  StyledHeader,
  StyledSubheader,
  StyledSubheader3,
  StyledSubheader4,
  StyledParagraph
} from '../styled';
import './events.css';
import '../shared/shared.css';

import exterminatus from '../../assets/exterminatus.jpeg';

function EventPage(props) {
  const [imageInModal, setImageInModal] = useState(null);
  function closeModal() {
    setImageInModal(null);
  }
  function openModal(src, alt) {
    setImageInModal({src, alt});
  }

  const { eventId } = props.match.params;
  const event = allEvents[eventId];
  if (!event) {
    return (
      <article>
        <StyledHeader>All records purged</StyledHeader>
        <img src={exterminatus} alt="" />
      </article>
    );
  }

  const { title, date, attacker, defender, gameType, gameDetails, titleCard, summary } = event;

  return (
    <article className="event-header">
      {imageInModal ? <ImageModal closeModal={closeModal} image={imageInModal} /> : null}

      <div className="event-header">
        <StyledHeader>{title}</StyledHeader>
        <StyledSubheader3>{attacker.faction} vs. {defender.faction}, {date}</StyledSubheader3>
        {gameType && <StyledSubheader4>{gameType}</StyledSubheader4>}
        {gameDetails && gameDetails.map(detail => <StyledSubheader4>{detail}</StyledSubheader4>)}
        <div className="thumb tnone">
          <div className="thumbinner" alt={title}>
            <a className="image" role="button" onClick={() => openModal(titleCard, title)}>
              <img className="thumbimage event-main" src={titleCard} alt={title} />
            </a>
          </div>
        </div>

        <section className="army-lists">
          <aside className="army-list attacker">
            <StyledSubheader>{attacker.faction}</StyledSubheader>
            <StyledSubheader3>{attacker.general}</StyledSubheader3>
            {attacker.picture &&
              <div className="thumb tnone">
                <div className="thumbinner" alt={attacker.general}>
                  <a
                    className="image"
                    role="button"
                    onClick={() => openModal(attacker.picture, attacker.general)}
                  >
                    <img className="thumbimage event-faction" src={attacker.picture} />
                  </a>
                </div>
              </div>
            }
            {attacker.armyList?.map(function(unit, i) {
              return <StyledParagraph key={`unit-${i}`}>{unit}</StyledParagraph>
            })}
          </aside>
          <aside className="army-list defender">
            <StyledSubheader>{defender.faction}</StyledSubheader>
            <StyledSubheader3>{defender.general}</StyledSubheader3>
            {defender.picture &&
              <div className="thumb tnone">
                <div className="thumbinner" alt={defender.general}>
                  <a
                    className="image"
                    role="button"
                    onClick={() => openModal(defender.picture, defender.general)}
                  >
                    <img className="thumbimage event-faction" src={defender.picture} />
                  </a>
                </div>
              </div>
            }
            {defender.armyList?.map(function(unit, i) {
              return <StyledParagraph key={`unit-${i}`}>{unit}</StyledParagraph>
            })}
          </aside>
        </section>
      </div>

      <section className="event-summary">
        {summary.map(function(paragraph, index) {
          return (
            <SectionParagraph
              key={`paragraph-${index}`}
              paragraph={paragraph}
              openModal={openModal}
            />
          );
        })}
      </section>
    </article>
  );
}

export default EventPage;